import React, { useState, useEffect } from "react";

const url = "https://api.github.com/users";

const User = () => {
  const [users, setUsers] = useState([]);

  const getUsers = async () => {
    const response = await fetch(url);
    const users = await response.json();

    setUsers(users);
  };

  useEffect(() => {
    getUsers();
  }, []);

  return (
    <>
      <h3 class="display-6 text-center text-uppercase">
        Company's Photographers
      </h3>
      <div class="container">
        <div class="row justify-content-center row-cols-3">
          {users.map((user) => {
            const { login, id, avatar_url, html_url } = user;
            return (
              <>
                <div class="" key={id} className="user">
                  <img
                    class="img-thumbnail"
                    width="200"
                    height="200"
                    src={avatar_url}
                    alt={login}
                  ></img>

                  <h4 class="text-uppercase fs-4">{login}</h4>
                  <a class="btn btn-dark" href={html_url}>
                    Profile
                  </a>
                </div>
              </>
            );
          })}
        </div>
      </div>
    </>
  );
};

export default User;
