import React, { useState } from "react";
import ReactDOM from "react-dom";
import User from "../Users/User";
import Service from "../Services/Service";

function Navbar() {
  const [isUsers, setIsUsers] = useState(true);
  const toHome = () => {
    ReactDOM.render(
      <>
        <User />
      </>,
      document.getElementById("content")
    );
    setIsUsers(false);
  };
  const changeContent = () => {
    if (!isUsers) {
      ReactDOM.render(
        <>
          <Service />
        </>,
        document.getElementById("content")
      );
      setIsUsers(true);
    } else {
      ReactDOM.render(
        <>
          <User />
        </>,
        document.getElementById("content")
      );
      setIsUsers(false);
    }
  };
  return (
    <>
      <ul class="nav d-flex justify-content-around bg-dark">
        <li class="nav-item">
          <button
            class="nav-link active btn btn-info text-light"
            aria-current="page"
            onClick={toHome}
          >
            Home
          </button>
        </li>
        <li class="nav-item">
          <button
            class="nav-link btn btn-info text-light"
            onClick={changeContent}
          >
            Products
          </button>
        </li>
        <li class="nav-item">
          <button
            class="nav-link disabled light bg-warning text-dark"
            href="#"
            tabindex="-1"
            aria-disabled="true"
            onClick=""
          >
            Christopher II's Photography Solution
          </button>
        </li>
      </ul>
    </>
  );
}

export default Navbar;
